*{padding:0;margin:0;box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;color:#646464;background-color:#e0eced;width:100%}.app{text-align:center;max-width:1260px;margin:0 auto}form{margin:2rem 0}input[type="text"],input[type="number"]{padding:0.6rem 0.8rem;margin:0 0.5rem;border:1px solid #bebebe}::placeholder{color:#bebebe}::-webkit-scrollbar{width:5px;cursor:pointer;background-color:white;border-right:1px solid #bebebe}::-webkit-scrollbar-thumb{background-color:#bebebe}::-webkit-scrollbar-thumb:hover{background-color:#ee604e}button,input[type="submit"],input[type="button"]{background-color:#aaa;color:#fff;padding:0.6rem 1.2rem;margin:0 0.5rem;border:none;transition:background-color 0.2s ease}button:hover,input[type="submit"]:hover,input[type="button"]:hover{background-color:#ee604e;cursor:pointer}button:focus,input[type="submit"]:focus,input[type="button"]:focus,input[type="text"]:focus{outline:none}.disabled{pointer-events:none;opacity:0.5}.header{margin:3rem 0}.header h1{font-size:2.5em;text-transform:uppercase;color:#ee604e;margin-bottom:1rem}.header button,.header input[type="submit"],.header input[type="button"]{text-transform:uppercase;margin:0 1rem}.container{display:flex;flex-wrap:wrap;justify-content:space-around}.container .section{background-color:#f3f9fa;border:1px solid #bebebe;margin:0 1rem 2rem 1rem}.container .timer{position:relative;padding:19.5rem;border-radius:50%;border-top-right-radius:0}.container .timer .tracking-task-text{position:absolute;left:50%;top:27%;transform:translateX(-50%);max-width:280px}.container .timer .counter{position:absolute;left:50%;top:50%;transform:translateX(-50%) translateY(-50%);color:#ee604e}.container .timer .counter .total-time{font-size:6em}.container .timer .counter p{text-align:right;margin-top:-10px;margin-right:10px}.container .timer .controls{position:absolute;left:50%;top:69%