te;left:50%;top:50%;transform:translateX(-50%) translateY(-50%);width:85px;height:85px;opacity:0.8;background-color:#ee604e;border-radius:50%}.container .timer .timer-cycle .stage-container .stage-content{position:absolute;left:50%;top:50%;transform:translateX(-50%) translateY(-50%);color:white}.container .timer .timer-cycle .stage-container .stage-content .stage-name{font-size:0.75em;text-transform:uppercase;letter-spacing:0.05em;line-height:1.15em}.container .timer .timer-cycle .stage-container .stage-content .stage-time{font-size:1.4em;font-weight:600}.container .timer .timer-cycle .passed-time{background-color:#bebebe}.container .todolist{padding:1.5rem 2.5rem}.container .todolist .section-title{color:#ee604e}.container .todolist h2{font-size:1.8em;font-weight:600}.container .todolist .edit-form label{display:block;font-size:0.9em}.container .todolist input[type="text"]{width:77%;margin:0.5rem 0}.container .todolist input[type="submit"]{margin:0;margin-left:3%}.container .todolist .tasks-list-title{background-color:#ddeff1;padding-top:1px;padding-bottom:2px;border:1px solid #bebebe}.container .todolist .tasks-list{height:350px;overflow-x:hidden;overflow-y:auto}.container .todolist .tasks-list .task{display:flex;justify-content:space-between;border:1px solid #bebebe;border-top:none}.container .todolist .tasks-list .task .status-marker{width:5px;height:auto;background-color:#aaa}.container .todolist .tasks-list .task .task-text-and-state{display:flex;flex-direction:column;text-align:left;width:300px;margin:0.4rem 0.8rem;justify-content:center}.container .todolist .tasks-list .task .task-text-and-state div{margin:0.3rem 0}.container .todolist .tasks-list .task .task-text-and-state .complete-btn{position:relative;cursor:pointer;color:#aaa}.container .todolist .tasks-list .task .task-text-and-state .complete-btn input{position:absolute;opacity:0;width:0;height:0}.container .todolist .tasks-list .task .task-text-and-state .complete-btn .checkmark:after{position:absolu